import React, { useState } from 'react'
import './App.css'
import isEqual from 'lodash.isequal'
import { useQuery, useSubscription, useApolloClient } from '@apollo/client'
import TopicSelection from './components/TopicSelection'
import Navbar from './components/Navbar'
import OldTopics from './components/OldTopics'
import loadingAnimation from './resources/loading.gif'
import AddTopic from './components/AddTopic'
import AdminView from './components/AdminView'
import Main from './components/Main'
import { GET_VOTE_STATE, VOTE_ADDED } from './queries/queries'
import Cookies from 'js-cookie'

const App = () => {
  const client = useApolloClient()

  const [body, setBody] = useState('main')
  const voteState = useQuery(GET_VOTE_STATE)

  useSubscription(VOTE_ADDED, {
    onSubscriptionData: ({ subscriptionData }) => {
      const newVoteState = subscriptionData.data.voteAdded

      const { getVoteState } = client.readQuery({
        query: GET_VOTE_STATE
      })

      if (!isEqual(newVoteState, getVoteState)) {
        console.log('ebin')
        console.log('newVoteState: ', newVoteState)
        client.writeQuery({
          query: GET_VOTE_STATE,
          data: {
            getVoteState: newVoteState
          }
        })
      }
    }
  })

  if (voteState.loading) {
    return (
      <div className='App'>
        <Navbar setBody={setBody} />
        <img className='Loading-animation' src={loadingAnimation} alt='loading animation' />
        <p>Loading...</p>
      </div>
    )
  }

  if (body === 'main') {
    return (
      <div className="App">
        <Navbar setBody={setBody} />
        <Main setBody={setBody} voteState={voteState.data.getVoteState} />
      </div>
    )
  }

  if (body === 'topicSelection') {
    return (
      <div className="App">
        <Navbar setBody={setBody} />
        <TopicSelection voteState={voteState.data.getVoteState} setBody={setBody} Cookies={Cookies} />
      </div>
    )
  }

  //if (body === 'covered') {
  //  return (
  //    <div className='App'>
  //      <Navbar setBody={setBody} />
  //      <OldTopics />
  //    </div>
  //  )
  //}

  if (body === 'addTopic') {
    return (
      <div className='App'>
        <Navbar setBody={setBody} />
        <AddTopic />
      </div>
    )
  }

  if (body === 'adminLogin') {
    return (
      <div className='App'>
        <Navbar setBody={setBody} />
        <AdminView voteState={voteState.data.getVoteState} />
      </div>
    )
  }

}

export default App
