import React, { useState, useEffect } from 'react'
import check from '../resources/chosen.png'
import ChoiceButton from './ChoiceButton'
import loadingAnimation from '../resources/loading.gif'
import { useQuery } from '@apollo/client'
import { GET_SPLITTED_LIST } from '../queries/queries'

const TopicSelection = ({ voteState, Cookies }) => {

  const [newRound, setNewRound] = useState(voteState.newRound)

  useEffect(() => {
    if (newRound) {
      Cookies.remove('voted')
    }
  }, [Cookies, newRound])

  const pool = useQuery(GET_SPLITTED_LIST)

  if (pool.loading) {
    return (
      <div className='App'>
        <div className='Container'>
          <img className='Loading-animation' src={loadingAnimation} alt='loading animation' />
        Loading...
      </div>
      </div>
    )
  }

  if (voteState === null) {
    return (
      <div className='App'>
        <h3 className='Heading'>Äänestys on suljettu.</h3>
      </div>
    )
  }

  if (voteState.voteOpen === false) {
    return (
      <div className='App'>
        <h3 className='Heading'>Äänestys on suljettu.</h3>
      </div>
    )
  }

  const leftList = pool.data.getSplittedList.leftList.topics
  const rightList = pool.data.getSplittedList.rightList.topics

  if (leftList.length === 0) {
    return (
      <div>
        <div className='Container'>
          <h1 className='Heading'>Voittaja-aihe</h1>
          {rightList.map(topic => <p key={topic.id} className='Topic-item'>{topic.name}</p>)}
        </div>
      </div>
    )
  }

  if (rightList.length === 0) {
    return (
      <div>
        <div className='Container'>
          <h1 className='Heading'>Voittaja-aihe</h1>
          {leftList.map(topic => <p key={topic.id} className='Topic-item'>{topic.name}</p>)}
        </div>
      </div>
    )
  }

  if (!Cookies.get('voted')) {
    return (
      <div>
        <div className='Container'>
          <p>Kierros: {voteState.round}</p>
          <p>Ääniä: {voteState.votes}/5</p>
          <h4 className='Heading'>Aihekategoriat <small>(Valitse jompikumpi)</small></h4>
          <div className='Container-flex'>
            <div className='Container-left'>
              {leftList.map(topic => <p key={topic.id} className='Topic-item'>{topic.name}</p>)}
            </div>
            <div className='Container-right'>
              {rightList.map(topic => <p key={topic.id} className='Topic-item'>{topic.name}</p>)}
            </div>
          </div>
          <div className='TopicSelection-buttons'>
            <ChoiceButton type='TopicSelection-Choice-button' text='Valitse' side='left' newRound={newRound} setNewRound={setNewRound} Cookies={Cookies} />
            <ChoiceButton type='TopicSelection-Choice-button' text='Valitse' side='right' newRound={newRound} setNewRound={setNewRound} Cookies={Cookies}  />
          </div>
        </div>
      </div>
    )
  } else {
    return (
      <div className='Container'>
        <div>
          <img className='Chosen-img' src={check} alt='Checked icon' />
          <p>Kierros: {voteState.round}</p>
          <p>Ääniä: {voteState.votes}/5</p>
          <p></p>
          <p>Valittu!</p>
          <p>Seuraava kierros alkaa kun kaikki ovat äänestäneet.</p>
        </div>
      </div>
    )
  }
}

export default TopicSelection
